import React from "react";
import withProvider from "../hoc/WithProvider";
import { Link } from "react-router-dom";

const Index = () => {
  return (
      <div className="ap-com container-main">
        <div className="ap-com sm-com-heading mb-4">
          <h3>Dashboard</h3>
        </div>
        <div className="ap-com quiqcc-com mb-4">
          <div className="qa-com qa-1  text-center">
            <Link to="/manage-users">
              <div className="qa-icon">
                <i className="fas fa-user"></i>
              </div>
              <div className="qa-content">
                <h3 className="fw-bold">350</h3>
                <p>Users Active</p>
              </div>
            </Link>
          </div>
          <div className="qa-com qa-2 text-center">
            <Link to="/manage-qr-codes">
              <div className="qa-icon">
                <i className="fas fa-qrcode"></i>
              </div>
              <div className="qa-content">
                <h3 className="fw-bold">15000+</h3>
                <p>QR Codes generated</p>
              </div>
            </Link>
          </div>
        </div>
      </div>
  );
};

export default withProvider(Index);
